<template>
    <v-container class="login" style="position: relative">
        <v-row>
            <v-col cols="12">
                <h2>{{accessToYourAccountText}}</h2>
            </v-col>
        </v-row>

        <v-col cols="12">
            <v-card>
                <v-progress-linear
                        value="100"
                        height="10"
                        color="blue-grey"
                        reactive
                        dark
                ></v-progress-linear>

                <v-container>
                    <v-form v-model="validForm">
                        <v-text-field
                                v-model="email"
                                :rules="[rules.required]"
                                :label="emailText"
                                required
                        ></v-text-field>

                        <v-text-field
                                v-model="password"
                                :rules="[rules.required]"
                                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                                :type="showPassword ? 'text' : 'password'"
                                :label="passwordText"
                                @click:append="showPassword = !showPassword"
                        ></v-text-field>
                    </v-form>

                    <v-btn block color="primary" @click="loginUser" :disabled="buttonIsDisabled">{{toLogInText}}</v-btn>
                </v-container>
            </v-card>
        </v-col>

        <v-col cols="12" align="center">
            <p>
                <router-link to="/send-change-password-email">{{haveYouForgottenYourPasswordText}}</router-link>
            </p>
        </v-col>

        <v-col cols="12" align="center" id="sign-up-now-claim">
            <p>{{stillDontHaveAnAccountText}}
                <router-link to="/sign-up">{{registerNowText}}</router-link>
            </p>
        </v-col>
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "login",
        data() {
            return {
                validForm: false,
                email: '',
                emailText: Constants.TEXT_EMAIL,
                password: '',
                passwordText: Constants.TEXT_PASSWORD,
                showPassword: false,
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText: '',
                accessToYourAccountText: Constants.TEXT_ACCESS_TO_YOUR_ACCOUNT,
                registerNowText: Constants.TEXT_REGISTER_NOW,
                haveYouForgottenYourPasswordText: Constants.TEXT_HAVE_YOU_FORGOTTEN_YOUR_PASSWORD,
                stillDontHaveAnAccountText: Constants.TEXT_STILL_DONT_HAVE_AN_ACCOUNT,
                rules: {
                    required: value => !!value || Constants.RULE_TEXT_MANDATORY_FIELD
                },
                closeText: Constants.TEXT_CLOSE,
                toLogInText: Constants.TEXT_TO_LOG_IN
            }
        },
        methods: {
            ...mapActions('user', ['login']),
            loginUser: function () {
                let self = this;
                let resultLogin = self.login({
                    email: self.email,
                    password: self.password
                });

                resultLogin.then(() => {
                    self.$store.dispatch('initAppInfo')
                        .then(() => {
                            self.$router.replace('dashboard');
                        })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                })
                    .catch(function (error) {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });

            },
        },
        watch: {
            validForm: function (value) {
                if (value) {
                    this.buttonIsDisabled = false
                } else {
                    this.buttonIsDisabled = true
                }
            }
        }
    }
</script>

<style scoped>

</style>
